

/* =================================
   COURSE SECTION - Cross-Platform Optimized
   ================================= */
.course-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40vh;
  padding: 40px 20px;
  position: relative;
  overflow: hidden;
  text-align: center;
  background: #faf8f5;
  padding-top: 20px;
  /* Consistent rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  padding-bottom: 150px;
}

.course-card {
  width: 900px;
  max-width: 95%; /* Prevent overflow on smaller screens */
  height: auto;
  padding: 20px;
  border-radius: 50px;
  background: #faf8f5;
  box-shadow: 
    20px 20px 60px rgba(0, 0, 0, 0.1),
    -20px -20px 60px rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
  /* Better cross-browser shadow rendering */
  position: relative;
  /* Prevent text selection issues on the card itself */
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.course-card p {
  color: #4a3728;
  letter-spacing: 0.5px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.05rem;
  padding: 10px 0;
  /* Cross-platform font consistency */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  line-height: 1.6;
  margin-top: 0;
  margin-bottom: 0;
}

/* Tablet - Enhanced */
@media (max-width: 768px) {
  .course-body {
    padding: 30px 15px;
    min-height: 35vh; /* Slightly smaller on tablets */
    padding-bottom: 100px;
  }
  
  .course-card {
    width: 90%;
    max-width: 600px; /* Prevent too wide on tablets */
    padding: 15px;
    border-radius: 20px;
  }
  
  .course-card p {
    font-size: clamp(0.95rem, 2.5vw, 1.05rem); /* Fluid scaling */
    width: 85%;
  }
}

/* Mobile - Enhanced */
@media (max-width: 480px) {
  .course-body {
    padding: 20px 10px;
    min-height: 30vh;
    padding-bottom: 100px;
  }
  
  .course-card {
    width: 95%;
    padding: 12px;
    border-radius: 15px;
    box-shadow: 
      10px 10px 30px rgba(0, 0, 0, 0.08),
      -10px -10px 30px rgba(255, 255, 255, 0.9);
  }
  
  .course-card p {
    font-size: clamp(0.85rem, 3vw, 0.95rem);
    width: 90%;
    letter-spacing: 0.3px; /* Slightly less letter spacing on mobile */
  }
}

/* Very small mobile screens */
@media (max-width: 320px) {
  .course-body {
    padding: 15px 8px;
  }
  
  .course-card {
    width: 98%;
    padding: 10px;
    border-radius: 12px;
  }
  
  .course-card p {
    font-size: 0.85rem;
    width: 95%;
    letter-spacing: 0.2px;
  }
}

/* High-DPI display optimization for course section */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .course-card {
    /* More subtle shadows on high-DPI displays */
    box-shadow: 
      15px 15px 45px rgba(0, 0, 0, 0.08),
      -15px -15px 45px rgba(255, 255, 255, 0.7);
  }
  
  @media (max-width: 480px) {
    .course-card {
      box-shadow: 
        8px 8px 25px rgba(0, 0, 0, 0.06),
        -8px -8px 25px rgba(255, 255, 255, 0.8);
    }
  }
}

/* Windows-specific improvements for course section */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .course-card {
    /* Simplified shadows for better Windows performance */
    box-shadow: 
      0 10px 30px rgba(0, 0, 0, 0.1),
      0 -5px 20px rgba(255, 255, 255, 0.5);
  }
  
  .course-card p {
    text-rendering: optimizeSpeed;
  }
}

/* Neumorphism enhancement for modern browsers */
@supports (backdrop-filter: blur(10px)) {
  .course-card {
    background: linear-gradient(145deg, #f0f0f0, #ffffff);
    border: 1px solid rgba(255, 255, 255, 0.3);
  }
}

/* Dark mode support (if implemented) */
@media (prefers-color-scheme: dark) {
  .course-body {
    background: #2a2a2a;
  }
  
  .course-card {
    background: #2a2a2a;
    box-shadow: 
      20px 20px 60px rgba(0, 0, 0, 0.3),
      -20px -20px 60px rgba(255, 255, 255, 0.05);
  }
  
  .course-card p {
    color: #e0e0e0;
  }
}