/* About Section Styles - Cross-Platform Optimized */
.about-body {
  background: #faf8f5;
  padding: 2rem;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Ensure consistent rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.about-title {
  font-style: italic;
  color: #6d4c41;
  opacity: 0.8;
  font-size: 2.5rem;
  font-weight: 600;
  text-align: left;
  margin-bottom: 2rem;
  letter-spacing: -0.02em;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  /* Font fallbacks for different systems */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.about-flex {
  display: flex;
  gap: 2rem;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  align-items: stretch;
  height: 250px;
  /* Ensure consistent box model */
  box-sizing: border-box;
}

/* Experience Cube - Cross-platform consistent */
.about-experience {
  background: linear-gradient(135deg, #6d4c41, #5d4037);
  color: white;
  padding: 2rem;
  border-radius: 23px;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex: 0 0 300px;
  box-shadow:
    0 8px 20px rgba(109, 76, 65, 0.3),
    0 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  /* Ensure consistent rendering */
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  /* Prevent text selection issues */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.about-experience::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  pointer-events: none;
  transform: scale(0);
  transition: transform 0.6s ease;
  /* Better performance across browsers */
  will-change: transform;
}

.about-experience:hover {
  transform: translateY(-5px);
  box-shadow:
    0 12px 30px rgba(109, 76, 65, 0.4),
    0 6px 15px rgba(0, 0, 0, 0.15);
}

.about-experience:hover::before {
  transform: scale(1);
}

.about-experience::after {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 18px;
  pointer-events: none;
}

.experience-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  /* Ensure consistent icon rendering */
  line-height: 1;
  display: block;
}

.counter-text {
  font-size: 1.6rem;
  font-weight: 800;
  /* Better number rendering */
  font-variant-numeric: lining-nums;
}

.experience-label {
  font-size: 1rem;
  opacity: 0.9;
}

/* Description with Arc and Background - Enhanced */
.about-description {
  background: linear-gradient(rgba(245, 245, 220, 0.9), rgba(222, 184, 135, 0.8));
  color: #4a3728;
  padding: 2rem;
  border-radius: 16px;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  font-size: 1rem;
  line-height: 1.6;
  flex: 1;
  display: flex;
  align-items: center;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  text-align: center;
  /* Better text rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

/* Background overlay for description */
.about-description::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: var(--bg-image);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.1;
  z-index: 0;
  /* Better background rendering */
  backface-visibility: hidden;
}

.about-description > * {
  position: relative;
  z-index: 1;
}

/* Enhanced Mobile Responsive with better breakpoints */
@media (max-width: 768px) {
  .about-body {
    padding: 1rem;
    height: auto;
    min-height: 50vh;
  }
  
  .about-title {
    font-size: clamp(1.75rem, 4vw, 2rem); /* Fluid scaling */
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  .about-flex {
    flex-direction: column;
    gap: 1.5rem;
    height: auto;
  }
  
  .about-experience {
    flex: none;
    height: 150px;
    font-size: 1.2rem;
    /* Better touch targets on mobile */
    min-height: 44px;
  }
  
  .experience-icon {
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
  }
  
  .counter-text {
    font-size: 1.4rem;
  }
  
  .about-description {
    padding: 1.5rem;
    font-size: 0.95rem;
    border-radius: 16px;
    min-height: 200px;
    /* Remove complex border radius on mobile for better rendering */
    border-top-right-radius: 16px;
    border-bottom-right-radius: 16px;
  }
}

/* Small Mobile with better scaling */
@media (max-width: 480px) {
  .about-body {
    padding: 0.75rem;
  }
  
  .about-title {
    font-size: clamp(1.5rem, 5vw, 1.75rem);
  }
  
  .about-experience {
    padding: 1.5rem;
    height: 130px;
  }
  
  .experience-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .counter-text {
    font-size: 1.2rem;
  }
  
  .about-description {
    padding: 1.25rem;
    font-size: 0.9rem;
  }
}

/* High-DPI display optimization */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .about-experience,
  .about-description {
    /* Slightly thinner borders on high-DPI displays */
    box-shadow: 
      0 4px 10px rgba(109, 76, 65, 0.25),
      0 2px 5px rgba(0, 0, 0, 0.08);
  }
}

/* Windows-specific font rendering improvements */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .about-title,
  .about-experience,
  .about-description {
    text-rendering: optimizeSpeed;
  }
}

/* Reduce motion for accessibility */
@media (prefers-reduced-motion: reduce) {
  .about-experience {
    transition: none;
  }
  
  .about-experience::before {
    transition: none;
  }
  
  .about-experience:hover {
    transform: none;
  }
}